import React from "react";
import CustTimeline from "../common/CustTimeline";

function Work(props) {
  let details = [
    {
      company: "IFS",
      title: "Trainee Software Developer",
      from: "01/07/2019",
      to: "31/12/2019",
    },
  ];
  return (
    <div style={{ height: "100%" }}>
      <CustTimeline
        type="work"
        work={details}
        theme={props.theme}
      ></CustTimeline>
    </div>
  );
}

export default Work;
