import React from "react";
import CustomCard from "../common/CustomCard";
import Skill from "../common/Skill";

function Skills(props) {
  const technicalSkills = [
    { type: "Java", level: 85 },
    { type: "MERN/MEAN ", level: 75 },
    { type: "Python", level: 75 },
    { type: "Flutter", level: 75 },
    { type: "SQL/NoSQL", level: 75 },
    { type: "Web Dev", level: 75 },
    { type: "Git", level: 75 },
  ];

  const createSkills = (skills) => {
    const _skills = [];

    for (let k = 0; k < skills.length; k++) {
      _skills.push(
        <Skill
          key={k}
          height={0.5}
          skill={skills[k].type}
          level={skills[k].level}
          color={props.color}
        ></Skill>
      );
    }

    return _skills;
  };

  let content = (
    <div className="col-center col-size pb-3 pt-2">
      <h1>Technical Skills</h1>
      {createSkills(technicalSkills)}
    </div>
  );
  return (
    <div>
      <CustomCard content={content}></CustomCard>
    </div>
  );
}

export default Skills;
