import React from "react";
import CustTimeline from "../common/CustTimeline";

function Education(props) {
  const details = [
    {
      school: "SLIIT - B.Sc. in IT",
      from: "2017",
      to: "2021",
    },
    {
      school: "Nalanda Central College",
      from: "2009",
      to: "2015",
    },
    {
      school: "Christ King College, Tudella",
      from: "2002",
      to: "2005",
    },
    {
      school: "Ja-ela Jayanthi Vidyalaya",
      from: "2005",
      to: "2009",
    },
  ];

  return (
    <div style={{ height: "100%" }}>
      <CustTimeline
        type="education"
        education={details}
        theme={props.theme}
      ></CustTimeline>
    </div>
  );
}

export default Education;
