import React from "react";
import Typist from "react-text-typist";

import CustomCard from "../common/CustomCard";

import whitePotrait from "../assets/white-potrait.png";
import yellowPotrait from "../assets/yellow-potrait.png";

import linkedIn from "../assets/linkedin.png";
import facebook from "../assets/facebook.png";
import twitter from "../assets/twitter.png";
import github from "../assets/github.png";

import AlternateEmailOutlinedIcon from "@material-ui/icons/AlternateEmailOutlined";
import Fab from "@material-ui/core/Fab";
import DownloadIcon from "@material-ui/icons/GetApp";

function About(props) {
  const image = props.theme === "light" ? yellowPotrait : whitePotrait;
  const msgs = ["Tech enthusiast", "Software Engineer", "Full Stack Developer"];

  let content = (
    <div className="col-center pt-3">
      <h1>Malaka Silva</h1>
      <div className="mb-3">
        <Typist
          className="typist"
          sentences={msgs}
          typingSpeed={200}
          deletingSpeed={80}
        />
      </div>
      <img src={image} alt="Malaka Silva" className="image" />
      <a href="mailto:malakasilva43@gmail.com" className="pt-2">
        <AlternateEmailOutlinedIcon />
        &nbsp;Contact me via email
      </a>
      <Fab
        aria-label="add"
        style={{
          position: "absolute",
          zIndex: 2,
          right: "30px",
          top: "40vh",
        }}
      >
        <DownloadIcon />
      </Fab>
      <p className="pl-4 pr-4 pt-3" style={{ textAlign: "center" }}>
        <a href="https://www.linkedin.com/in/malaka-silva/">
          <img src={linkedIn} alt="LinkedIn" className="connection-img" />
        </a>
        <a href="https://www.facebook.com/MalakaDSilva/">
          <img src={facebook} alt="Facebook" className="connection-img" />
        </a>
        <a href="https://www.twitter.com/_MalakaSilva_/">
          <img src={twitter} alt="Twitter" className="connection-img" />
        </a>
        <a href="https://github.com/MalakaDeSilva">
          <img
            src={github}
            alt="Github"
            className="connection-img"
            style={{ background: "white", borderRadius: "5px" }}
          />
        </a>
      </p>
    </div>
  );

  return <CustomCard content={content}></CustomCard>;
}

export default About;
